<style lang="less">
    @import './index.less';
</style>
<template>
    <div class="index-main">
        <div class='amount'>
            <label>{{this.wallet.amount}}</label> &nbsp;&nbsp;SDG
        </div>
        <div class="address">
            <label id="lblAddr">{{this.wallet.address}}</label>&nbsp;&nbsp;&nbsp;&nbsp;
            <Icon data-clipboard-target="#lblAddr" @click="copyAddress" class="copybtn hover" type="ios-copy-outline" color="#2d8cf0" size="24"></Icon>
            &nbsp;&nbsp;&nbsp;&nbsp;
            <i  @click="showAddressQR" class="icon-qr hover"></i>
        </div>
        <Card class="asset-con" >
            <div class="list-con" v-for="item in assets" :key="item.name"> 
                <div class="asset-detail">
                    <label>{{item.amount}}</label>&nbsp;&nbsp;<label>{{item.name}}</label>
                </div>
                <div class="asset-sdg">
                    &asymp;&nbsp;<label>{{item.sdg_price}}</label>&nbsp;&nbsp;<label>SDG</label>
                </div>
            </div>
        </Card>
        <Modal  v-model="showQR" width="400" class-name="qr-con vertical-center-modal">
            <div slot="header">
                {{this.wallet.address}}
            </div>
            
            <div id="qrcode">
            </div>
        </Modal>
    </div>
</template>
<script>
    import Clipboard from 'clipboard';
    import QR from 'qrcodejs2';
    export default {
        name:'index',
        data() {
            return {
                showQR:false,
                wallet:{
                    address:'',
                    amount:0
                },
                assets:[
                    {
                        name:'',
                        amount:'',
                        sdg_price:''
                    }
                ]
            };
        },
        methods: {
            init(){
                this.getBalance();
                this.interval_connect=setInterval(() => {
                       this.getBalance();
                    }, 3000);
                              
                
                this.wallet.address=this.$store.state.wallet.mainAddress;
                this.genQR(this.wallet.address);
                
                this.assets=[];
                let rlt=[
                    {
                        name:'ABC',
                        amount:'1029',
                        sdg_price:'21.32'
                    },
                    {
                        name:'XYZ',
                        amount:'2191.18912',
                        sdg_price:'32'
                    },
                    {
                        name:'QIG',
                        amount:'218912.000',
                        sdg_price:'1128.0000'
                    }
                ];
                // this.assets=rlt;
            },
            getBalance(){
                this.$store.state.wallet.getBalance().then(
                    (rep)=>{
                        this.wallet.amount= rep/1000000;
                    },
                    (error)=>
                    {
                        console.log('get amount---',error);
                    }
                );  
            },
            copyAddress(){
                let clipboard = new Clipboard('.copybtn');
                
                clipboard.on('success',e =>{
                    this.$Message.success(this.$t('page_index.alert.clipboard_success'));
                    clipboard.destroy();
                });
                clipboard.on('error',e =>{
                    this.$Message.success(this.$t('page_index.alert.clipboard_error'));
                    clipboard.destroy();
                });
            },
            showAddressQR()
            {  
                this.showQR=true;
                
            },
            genQR(text)
            {
                let qrcode= new QR('qrcode', {  
                        width: 200,  
                        height: 200,
                        text:text
                    });
                return qrcode;
            }
            
        },
        mounted() {
            this.init();

        },
        activated() {
            this.$store.state.wallet.getBalance().then(
                (rep)=>{
                    this.wallet.amount= rep/1000000;
                },
                (error)=>
                {
                    console.log('get amount---',error);
                }
            );
        },
        destroyed() {
            clearInterval(this.interval_connect);
        },
        computed: {
            interval_balance: {
                set (val) {
                    this.$store.state.interval.balance = val;
                },
                get() {
                    return this.$store.state.interval.balance;
                }
            },
        },
    };
</script>


